*{
  background-color:#39495E
}
.next-btn{
  display: flex;
  float: right;
  position: fixed;
  top: 52%;
  right: 2.2%;
  
}
.next-btn button{
  background: #14AFF1;
  border: none;
  font-weight: 2px;
