  font-size: 25px;
  border-radius: 100%;
  width: 45px;
  height: 45px;
  cursor: pointer;
  box-shadow: 0px 2px 6px #00000069;
}

.prev-btn{
  display: flex;
  float: right;
  position: fixed;
  top: 52%;
  left: 1.4%;
  
}
.prev-btn button{
  background: #39495E;
  border: none;
  font-weight: 2px;
  color: #97A1A9;
  font-size: 25px;
  border-radius: 100%;
  width: 45px;
  height: 45px;
  cursor: pointer;
  box-shadow: 0px 2px 6px #00000069;
  
}



/*Add Modal*/

.add-modal-bg{
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.5s, opacity 0.5s;
}

.add-modal-active{
  visibility: visible;
  opacity: 1;
}

.add-modal{
  background-color: #2A3E4C;
  width: 57%;
  height: 52%;
  align-items: left;
  flex-direction: column;
  border-radius: 8px;
}


/*Add Modal Header*/

.add-modal-header{
  color:#eceeef;
  border-bottom: #243441 2px solid;
  padding-top: 18px;
  padding-bottom: 18px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 22px;
  letter-spacing: 0.19px;

}

.add-modal-close{
  float: right;
  cursor: pointer;
}


/*Add Modal Body*/


.add-modal-content{
  padding: 20px 10px 20px 25px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 50px;
}

.add-modal-content input{
  color: white;
  letter-spacing: 1px;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  padding-left: 10px;
}


.add-modal label{
  color:#97A1A9;
  letter-spacing: 0.19px;
  padding-right: 30px;
}

input[type="modal-txt"]{
  background: #283a46;
  border: 1.5px solid #31556a;
  border-radius: 8px;
  width: 220px;
  height: 34px;
  outline: none;

}

.customer-no{
  margin-top: 20px;
}

.customer-no input{
  margin-left: 17px;
}


.invoice-no{
  margin-top: 20px;
}

.invoice-no input{
  margin-left: 35px;
}

.invoice-amt{
  margin-top: 20px;
}

.invoice-amt input{
  margin-left: 3px;
}

.due-date input{
  margin-left: 13px;
  background: #283a46;
  border: 1.5px solid #31556a;
  border-radius: 8px;
  width: 220px;
  height: 34px;
  outline: none;
}


.notes{
  margin-top: 20px;
  grid-column-start: 2;
  grid-row-start: 2;
  grid-row-end: 6;
  display: flex;
}

.notes input{
  margin-left: 42px;
}

input[type="notes-txt"]{
  background: #283a46;
  border: 1.5px solid #31556a;
  border-radius: 8px;
  width: 220px;
  height: 148px;
  outline: none;

}

.notes label{
  float: top;
}

/*Add Modal Footer*/


.add-modal-footer{
  margin-top: 0px;
  border-top: #243441 2px solid;
  padding-top: 15px;
  padding-bottom: 0px;
  padding-left: 20px;
  padding-right: 20px;

}

.modal-add-btn{
  float: right;
  background:#14AFF1;
  border-radius: 10px;
  width: 72px;
  height: 38px;
  border: none;
  color: #eceeef;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
  padding-bottom: 5px;
}

.modal-add-btn a{
  color: #eceeef;
}

.modal-clear-btn{
  float: right;
  background:none;
  border: 1px solid #14AFF1;
  border-radius: 10px;
  width: 72px;
  height: 38px;
  margin-right: 20px;
  padding-bottom: 5px;
  color: #eceeef;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
}

.modal-clear-btn a{
  color: #eceeef;
}

.modal-cancel-btn{
  float: left;
  background:none;
  width: 72px;
  height: 38px;
  margin-right: 20px;
  padding-bottom: 5px;
  border: none;
  font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 18px;
}

.modal-cancel-btn a{
  color: #14AFF1;
}








